.jumbotron-mobile {
  margin-top: 40px;
  margin-bottom: 30px;
}
.jumbotron-mobile__bottom {
  width: 95.9%;
  display: flex;
  justify-content: space-between;
  margin-left: auto;
  margin-right: auto;
  margin-top: -175px;
}
.jumbotron-mobile__item .btn {
  margin-left: auto;
  margin-right: auto;
  margin-top: 35px;
}
.jumbotron-mobile__img {
  width: 100%;
}
@media (max-width: 500px) {
  .jumbotron-mobile {
    margin-top: 30px;
    margin-bottom: 20px;
  }
  .jumbotron-mobile__bottom {
    width: 95%;
    margin-top: 10px;
  }
  .jumbotron-mobile__QR {
    height: 120px;
    width: 120px;
  }
  .jumbotron-mobile__item .btn {
    margin-top: 15px;
  }
}
.services-types {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: space-between;
  margin-left: -10px;
  margin-right: -10px;
}
.services-types .card {
  position: relative;
  padding: 30px 20px 43px 40px;
  width: 350px;
  height: 430px;
  border-radius: 6px;
  margin-top: 10px;
  margin-bottom: 20px;
  margin-left: 10px;
  margin-right: 10px;
}
.services-types .card__bg {
  opacity: 0.6;
  border-radius: 6px;
}
.services-types .card__content .btn {
  margin-top: auto;
}
.services-types .card__icon {
  margin-bottom: 22px;
}
.services-types .card__title,
.services-types .card__text {
  color: #fff;
  margin-left: 8px;
}
.services-types .card__title {
  font-weight: 600;
  margin-bottom: 15px;
}
@media (max-width: 1160px) {
  .services-types {
    justify-content: space-evenly;
  }
}
@media (max-width: 768px) {
  .services-types .card {
    width: 300px;
    height: 390px;
    padding: 20px;
  }
}
@media (max-width: 500px) {
  .services-types .card {
    width: 100%;
    padding: 10px;
    padding-bottom: 20px;
  }
  .services-types .card svg.icon-family-svg {
    width: 106px;
    height: 114px;
  }
  .services-types .card svg.icon-case-svg {
    width: 106px;
    height: 106px;
  }
}
html.scale .services-types .card {
  padding-top: 20px;
  padding-bottom: 30px;
  padding-left: 20px;
}
html.color-white .services-types .card {
  border: 2px solid #000;
}
html.color-white .services-types .card__icon * {
  fill: #161616;
}
html.color-white .services-types .card__title,
html.color-white .services-types .card__text {
  color: #161616;
}
html.color-black .services-types .card {
  border: 2px solid #fff;
}
html.color-black .services-types .card__icon * {
  fill: #fff;
}
html.color-black .services-types .card__title,
html.color-black .services-types .card__text {
  color: #fff;
}
.section-services-type > .btn {
  margin-top: 20px;
}
.services-type-grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-left: -10px;
  margin-right: -10px;
}
.services-type-grid .card {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 240px;
  height: 300px;
  padding: 42px 28px 30px 28px;
  margin-left: 10px;
  margin-right: 10px;
  margin-bottom: 20px;
  border-radius: 6px;
  transition: all 0.25s ease-in-out;
}
.services-type-grid .card__bg {
  opacity: 0.6;
  border-radius: 6px;
}
.services-type-grid .card__icon {
  margin-bottom: 25px;
  z-index: 2;
}
.services-type-grid .card__icon_mobile {
  margin-left: 10px;
}
.services-type-grid .card__icon_pipe {
  margin-top: 20px;
  margin-bottom: 40px;
}
.services-type-grid .card__icon_lab {
  margin-bottom: 48px;
}
.services-type-grid .card__icon_tap {
  margin-bottom: 45px;
}
.services-type-grid .card__icon_truck {
  margin-top: 20px;
  margin-bottom: 52px;
}
.services-type-grid .card__icon_build-tools {
  margin-top: 10px;
  margin-bottom: 50px;
}
.services-type-grid .card__icon_dump-truck {
  margin-top: 10px;
  margin-bottom: 65px;
}
.services-type-grid .card__title {
  color: #fff;
  font-size: 1.28571429rem;
  font-weight: 600;
  line-height: 1.78571429rem;
  margin-bottom: 15px;
}
.services-type-grid .card .btn {
  margin-top: auto;
}
@media (max-width: 1160px) {
  .services-type-grid {
    justify-content: space-evenly;
  }
}
@media (max-width: 850px) {
  .services-type-grid .card {
    padding: 20px;
    width: 230px;
    height: 280px;
  }
  .services-type-grid .card__title {
    font-size: 1.14285714rem;
    line-height: 1.42857143rem;
  }
}
@media (max-width: 500px) {
  .services-type-grid .card {
    width: 100%;
    height: auto;
    min-height: 250px;
    margin-bottom: 25px;
  }
  .services-type-grid .card__bg {
    filter: blur(3px);
  }
  .services-type-grid .card__icon {
    margin-top: 0;
    margin-left: 0;
    margin-bottom: 20px;
  }
  .services-type-grid .card__title {
    margin-bottom: 15px;
  }
  .services-type-grid .card__title br {
    display: none;
  }
  .services-type-grid .card__content {
    width: 100%;
  }
  .services-type-grid .card__content .btn {
    margin-left: auto;
    margin-right: auto;
    padding: 10px 20px;
    font-size: 1.07142857rem;
  }
}
html.scale .services-type-grid .card {
  padding: 20px;
}
html.color-white .services-type-grid .card {
  border: 2px solid #000;
}
html.color-white .services-type-grid .card__title {
  color: #161616;
}
html.color-black .services-type-grid .card {
  border: 2px solid #fff;
}
html.color-black .services-type-grid .card__title {
  color: #fff;
}
